<template>
  <div id="register" class="register">
    <Navbar />

    <div class="container main">
      <div class="col-lg-10 offset-1 col-md-10 col-sm-12 col-xs-12">
        <div class="row">
          <div class="col-md-6 col-sm-12 col-xs-12">
            <div class="card card-inspirations">
              <div class="card-heading">
                <div class="card-img">
                  <a href="#">
                    <img src="./../assets/images/img-1.png" alt="">
                  </a>
                </div>
              </div>
              <div class="card-main">
                <p><span class="posted-by">Posted By <strong>Jebor</strong></span> <strong>/</strong> <span class="category">Photography, Branding</span></p>
              </div>
            </div>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12">
            <div class="register-main">
              <div class="heading">
                <span>SIgn up</span>
                <h3>Go, Sign up!</h3>
              </div>
              <form>
                <div class="form-group">
                  <label>Name</label>
                  <input type="text" class="form-control" placeholder="Enter your name">
                </div>
                <div class="form-group">
                  <label>Email</label>
                  <input type="email" class="form-control" placeholder="Enter your email" value="Jebor@simalakama.com">
                </div>
                <div class="form-group">
                  <label>Password</label>
                  <input type="password" class="form-control" id="exampleInputPassword1" placeholder="Input your password">
                </div>
                <div class="form-group">
                  <button type="submit" class="btn btn-primary">Sign Up</button>
                  <span>or</span>
                  <button type="button" class="btn btn-link">Sign In</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Navbar from '@/components/Navbar.vue';

export default {
  name: 'Register',
  components: {
    Navbar,
  }
}
</script>



